@use '../../styles/colors.scss' as *;


.footer{
    padding: 104px 0 72px;
    background-color: $black;

    .nav{
        padding: 40px 0;

        @media (max-width:768px){

            .container{
                position: relative;
                flex-direction: column;
                gap: 65px;
                justify-content: center;

                &::after{
                    content: '';
                    top: 50px;
                    position: absolute;
                    display: block;
                    width: 100%;
                    height: 1px;
                    background-color: rgba($white,0.1);
                }

                .nav__links{
                    flex-direction: column;
                    gap: 32px;
                    justify-content: center;
                }
            }
        }
    }

    &__info{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-end;
        padding-top: 30px;
        border-top:1px solid  rgba($white,0.1);
        color: rgba($white,0.5);
        line-height: 1.6;

        @media (max-width:768px){
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 42px;
            text-align: center;
            border-top:none;
        }
       
    }

    &__office li:first-child,
    &__contacts li:first-child{
        font-weight: bold;
    }
    
    &__social{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        gap: 16px;

        li a svg path{
           transition: fill 0.4s ease-in;
        }

        li:hover a svg path{
            fill: $light-peach;
        }
    }
}

.footer-contacts{
    padding: 72px 0 72px;
}